import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatTableModule } from '@angular/material/table';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatSortModule } from '@angular/material/sort';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatChipsModule } from '@angular/material/chips';
import { MatDialogModule } from '@angular/material/dialog';
import { MatSnackBarModule } from '@angular/material/snack-bar';

@Component({
  selector: 'app-agencias',
  standalone: true,
  imports: [
    CommonModule,
    MatCardModule,
    MatIconModule,
    MatButtonModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatChipsModule,
    MatDialogModule,
    MatSnackBarModule
  ],
  template: `
    <div class="p-6">
      <div class="mb-6">
        <h1 class="text-3xl font-bold text-gray-800 mb-2">Catálogo de Agencias</h1>
        <p class="text-gray-600">Gestión del catálogo maestro de agencias del sistema del expediente único</p>
      </div>

      <!-- Acciones Principales -->
      <div class="mb-6">
        <button mat-raised-button color="primary" class="mr-4">
          <mat-icon>add</mat-icon>
          Nueva Agencia
        </button>
        <button mat-button color="accent">
          <mat-icon>upload</mat-icon>
          Importar Catálogo
        </button>
        <button mat-button>
          <mat-icon>download</mat-icon>
          Exportar Catálogo
        </button>
        <button mat-button>
          <mat-icon>refresh</mat-icon>
          Sincronizar
        </button>
      </div>

      <!-- Filtros de Búsqueda -->
      <mat-card class="mb-6">
        <mat-card-content class="p-4">
          <div class="grid grid-cols-1 md:grid-cols-4 gap-4">
            <mat-form-field class="w-full">
              <mat-label>Buscar Agencia</mat-label>
              <input matInput placeholder="Nombre, código o ubicación">
              <mat-icon matSuffix>search</mat-icon>
            </mat-form-field>
            
            <mat-form-field class="w-full">
              <mat-label>Estado</mat-label>
              <mat-select>
                <mat-option value="todos">Todas las Agencias</mat-option>
                <mat-option value="activa">Activa</mat-option>
                <mat-option value="inactiva">Inactiva</mat-option>
                <mat-option value="suspension">En Suspensión</mat-option>
              </mat-select>
            </mat-form-field>
            
            <mat-form-field class="w-full">
              <mat-label>Tipo de Agencia</mat-label>
              <mat-select>
                <mat-option value="todos">Todos los Tipos</mat-option>
                <mat-option value="principal">Principal</mat-option>
                <mat-option value="sucursal">Sucursal</mat-option>
                <mat-option value="satelite">Satélite</mat-option>
                <mat-option value="temporal">Temporal</mat-option>
              </mat-select>
            </mat-form-field>
            
            <mat-form-field class="w-full">
              <mat-label>Región</mat-label>
              <mat-select>
                <mat-option value="todos">Todas las Regiones</mat-option>
                <mat-option value="norte">Norte</mat-option>
                <mat-option value="sur">Sur</mat-option>
                <mat-option value="este">Este</mat-option>
                <mat-option value="oeste">Oeste</mat-option>
                <mat-option value="centro">Centro</mat-option>
              </mat-select>
            </mat-form-field>
          </div>
        </mat-card-content>
      </mat-card>

      <!-- Estadísticas del Catálogo -->
      <div class="grid grid-cols-1 md:grid-cols-4 gap-6 mb-6">
        <mat-card class="bg-blue-50 border-l-4 border-blue-500">
          <mat-card-content class="p-4">
            <div class="flex items-center">
              <mat-icon class="text-blue-600 mr-3">business</mat-icon>
              <div>
                <div class="text-2xl font-bold text-blue-600">156</div>
                <div class="text-sm text-blue-600">Total de Agencias</div>
              </div>
            </div>
          </mat-card-content>
        </mat-card>

        <mat-card class="bg-green-50 border-l-4 border-green-500">
          <mat-card-content class="p-4">
            <div class="flex items-center">
              <mat-icon class="text-green-600 mr-3">check_circle</mat-icon>
              <div>
                <div class="text-2xl font-bold text-green-600">142</div>
                <div class="text-sm text-green-600">Agencias Activas</div>
              </div>
            </div>
          </mat-card-content>
        </mat-card>

        <mat-card class="bg-orange-50 border-l-4 border-orange-500">
          <mat-card-content class="p-4">
            <div class="flex items-center">
              <mat-icon class="text-orange-600 mr-3">pending</mat-icon>
              <div>
                <div class="text-2xl font-bold text-orange-600">8</div>
                <div class="text-sm text-orange-600">En Suspensión</div>
              </div>
            </div>
          </mat-card-content>
        </mat-card>

        <mat-card class="bg-red-50 border-l-4 border-red-500">
          <mat-card-content class="p-4">
            <div class="flex items-center">
              <mat-icon class="text-red-600 mr-3">cancel</mat-icon>
              <div>
                <div class="text-2xl font-bold text-red-600">6</div>
                <div class="text-sm text-red-600">Inactivas</div>
              </div>
            </div>
          </mat-card-content>
        </mat-card>
      </div>

      <!-- Tabla de Agencias -->
      <mat-card>
        <mat-card-header>
          <mat-card-title>Lista de Agencias</mat-card-title>
          <mat-card-subtitle>Catálogo completo de agencias registradas en el sistema</mat-card-subtitle>
        </mat-card-header>
        <mat-card-content class="p-0">
          <table mat-table [dataSource]="agencias" class="w-full">
            <ng-container matColumnDef="codigo">
              <th mat-header-cell *matHeaderCellDef>Código</th>
              <td mat-cell *matCellDef="let agencia">
                <div class="font-mono text-sm bg-gray-100 px-2 py-1 rounded">
                  {{agencia.codigo}}
                </div>
              </td>
            </ng-container>

            <ng-container matColumnDef="nombre">
              <th mat-header-cell *matHeaderCellDef>Nombre</th>
              <td mat-cell *matCellDef="let agencia">
                <div class="flex items-center">
                  <div class="w-10 h-10 bg-blue-100 rounded-full flex items-center justify-center mr-3">
                    <mat-icon class="text-blue-600 text-lg">business</mat-icon>
                  </div>
                  <div>
                    <div class="font-medium">{{agencia.nombre}}</div>
                    <div class="text-sm text-gray-500">{{agencia.direccion}}</div>
                  </div>
                </div>
              </td>
            </ng-container>

            <ng-container matColumnDef="tipo">
              <th mat-header-cell *matHeaderCellDef>Tipo</th>
              <td mat-cell *matCellDef="let agencia">
                <mat-chip [color]="getTipoColor(agencia.tipo)" selected>
                  {{agencia.tipo}}
                </mat-chip>
              </td>
            </ng-container>

            <ng-container matColumnDef="region">
              <th mat-header-cell *matHeaderCellDef>Región</th>
              <td mat-cell *matCellDef="let agencia">
                <span class="px-2 py-1 bg-gray-100 rounded text-xs">{{agencia.region}}</span>
              </td>
            </ng-container>

            <ng-container matColumnDef="estado">
              <th mat-header-cell *matHeaderCellDef>Estado</th>
              <td mat-cell *matCellDef="let agencia">
                <span class="px-2 py-1 rounded text-xs" 
                      [class]="getEstadoClass(agencia.estado)">
                  {{agencia.estado}}
                </span>
              </td>
            </ng-container>

            <ng-container matColumnDef="ultimaActualizacion">
              <th mat-header-cell *matHeaderCellDef>Última Actualización</th>
              <td mat-cell *matCellDef="let agencia">{{agencia.ultimaActualizacion}}</td>
            </ng-container>

            <ng-container matColumnDef="acciones">
              <th mat-header-cell *matHeaderCellDef>Acciones</th>
              <td mat-cell *matCellDef="let agencia">
                <button mat-icon-button color="primary" class="mr-2" title="Editar">
                  <mat-icon>edit</mat-icon>
                </button>
                <button mat-icon-button color="accent" class="mr-2" title="Ver Detalles">
                  <mat-icon>visibility</mat-icon>
                </button>
                <button mat-icon-button color="warn" title="Eliminar">
                  <mat-icon>delete</mat-icon>
                </button>
              </td>
            </ng-container>

            <tr mat-header-row *matHeaderRowDef="columnasAgencias"></tr>
            <tr mat-row *matRowDef="let row; columns: columnasAgencias;"></tr>
          </table>
        </mat-card-content>
        <mat-card-actions>
          <button mat-button color="primary">
            <mat-icon>add</mat-icon>
            Agregar Agencia
          </button>
          <button mat-button color="accent">
            <mat-icon>sync</mat-icon>
            Sincronizar Catálogo
          </button>
          <button mat-button>
            <mat-icon>report</mat-icon>
            Generar Reporte
          </button>
        </mat-card-actions>
      </mat-card>
    </div>
  `,
  styles: [`
    .grid {
      display: grid;
    }
    
    .grid-cols-1 {
      grid-template-columns: repeat(1, minmax(0, 1fr));
    }
    
    .md\\:grid-cols-4 {
      grid-template-columns: repeat(4, minmax(0, 1fr));
    }
    
    .gap-4 {
      gap: 1rem;
    }
    
    .gap-6 {
      gap: 1.5rem;
    }
    
    .w-full {
      width: 100%;
    }
    
    .border-l-4 {
      border-left-width: 4px;
    }
    
    .font-mono {
      font-family: ui-monospace, SFMono-Regular, "SF Mono", Consolas, "Liberation Mono", Menlo, monospace;
    }
  `]
})
export class AgenciasComponent {
  agencias = [
    {
      codigo: 'AG-001',
      nombre: 'Agencia Central',
      direccion: 'Av. Principal 123, Centro',
      tipo: 'Principal',
      region: 'Centro',
      estado: 'Activa',
      ultimaActualizacion: 'Hace 2 horas'
    },
    {
      codigo: 'AG-002',
      nombre: 'Sucursal Norte',
      direccion: 'Calle Norte 456, Zona Norte',
      tipo: 'Sucursal',
      region: 'Norte',
      estado: 'Activa',
      ultimaActualizacion: 'Hace 1 día'
    },
    {
      codigo: 'AG-003',
      nombre: 'Oficina Sur',
      direccion: 'Blvd. Sur 789, Zona Sur',
      tipo: 'Sucursal',
      region: 'Sur',
      estado: 'Activa',
      ultimaActualizacion: 'Hace 3 días'
    },
    {
      codigo: 'AG-004',
      nombre: 'Punto Este',
      direccion: 'Calle Este 321, Zona Este',
      tipo: 'Satélite',
      region: 'Este',
      estado: 'En Suspensión',
      ultimaActualizacion: 'Hace 1 semana'
    },
    {
      codigo: 'AG-005',
      nombre: 'Oficina Oeste',
      direccion: 'Av. Oeste 654, Zona Oeste',
      tipo: 'Sucursal',
      region: 'Oeste',
      estado: 'Activa',
      ultimaActualizacion: 'Hace 5 días'
    }
  ];

  columnasAgencias = ['codigo', 'nombre', 'tipo', 'region', 'estado', 'ultimaActualizacion', 'acciones'];

  getTipoColor(tipo: string): string {
    switch (tipo) {
      case 'Principal': return 'warn';
      case 'Sucursal': return 'primary';
      case 'Satélite': return 'accent';
      case 'Temporal': return 'primary';
      default: return 'primary';
    }
  }

  getEstadoClass(estado: string): string {
    switch (estado) {
      case 'Activa': return 'bg-green-100 text-green-800';
      case 'Inactiva': return 'bg-red-100 text-red-800';
      case 'En Suspensión': return 'bg-orange-100 text-orange-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  }

  constructor() {}
}
